<template>
    <div id="mine">
        <div class="userInfo">
            <mt-cell title="" value=""></mt-cell>
            <mt-cell title="" value=""></mt-cell>
            <mt-cell title="" value="15555555555"></mt-cell>
            <mt-cell title="" is-link @click.native="changePassword()">
            </mt-cell>
        </div>
    </div>
</template>

<script>
import { Cell,Button,MessageBox,Toast } from 'mint-ui';
export default {
    name: 'mine',
    data(){
        return{

        }
    },
    methods: {
        changePassword(){
            MessageBox.prompt('', {
            inputValidator: (val) => {
                if (val === null) {
                    return true;    //nullMessageBox
                }
                return !(val.length < 6 || val.length > 8)
            }, inputErrorMessage: '6~8,'
            }).then((val) => {
                console.log(val)
                MessageBox.confirm('?').then(action => {
                    Toast({
                        message: '',
                        iconClass: 'el-icon-check'
                    });
                });
            }, () => {
                console.log('cancel')
            })

        }
    },
    mounted() {
        
    },
}
</script>

<style>
#mine{
    width: 100%;
    height: 100%;
}
#mine .userInfo{
    margin-top: 30px;
}
</style>


