<template>
    <div id="menu4">
        <ul class="fourthmenu">
            <li v-if="menulist" class="menuList" v-for="(item,k) in menulist" :key="k" @click="link(item)">
                <span>{{item.name}}</span>
            </li>
        </ul>
        <div class="action">
            <mt-button class="backbtn" type="default" @click="back()"></mt-button>
            <mt-button class="quickbtn" type="default" size="normal" @click="sheetVisible=true"></mt-button>
            <mt-actionsheet :actions="data" v-model="sheetVisible">
            </mt-actionsheet>
        </div>
    </div>
</template>

<script>
export default {
    name: 'fourthMenu',
    data(){
        return{
            sheetVisible: false,
            data: [
                {name:'',nav:'xqhxzb',method:this.action}, 
                {name:'',nav:'xxjcsj',method:this.action}, 
                {name:'',nav:'zyfzsj',method:this.action}, 
                {name:'',nav:'kcjssj',method:this.action}, 
                {name:'',nav:'szdwsj',method:this.action}, 
                {name:'',nav:'xsxxsj',method:this.action},
                {name:'',nav:'jcfx',method:this.action},
                {name:'',nav:'zwzdbg',method:this.action}
            ],
            bgcolor: ['#768395','#7CB349','#358FE2','#505050','#E42739','#809295','#EF8629','#C6D90F'],
            menulist: [],
            navlist: [
                {
                    navName:[
                        {name:'',id:'1',reg:'bxdw',route:'xqhxzb'},
                        {name:'',id:'2',reg:'rcpymb'},
                        {name:'',id:'3',reg:'szjy',route:'xqhxzb'},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'4',reg:'zyjszt'},
                        {name:'',id:'5',reg:'kcjszt'},
                        {name:'',id:'6',reg:'ktjxysjjx'},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'7',reg:'zyjsdw'},
                        {name:'',id:'8',reg:'jzjsdw'},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'9',reg:'jxtrjjcss'},
                        {name:'',id:'10',reg:'jxzyjszt'},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'11',reg:'xqhzzt'},
                        {name:'',id:'12',reg:'xxglzdjsyyxzt'},
                        {name:'',id:'13',reg:'zljkzt'},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'14',reg:'xsfk'},
                        {name:'',id:'15',reg:'yrffk'},
                        {name:'',id:'16',reg:'qt'},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'17',reg:'sxsb'},
                        {name:'',id:'18',reg:'xxxszdmj'},
                        {name:'',id:'19',reg:'tsgcstbt'},
                        {name:'',id:'20',reg:'jxsb'},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'21',reg:'bxlnbxdw',title:'--',report:true},
                        {name:'',id:'22',reg:'bxlnrcpymb',title:'--',report:true},
                        {name:'',id:'23',reg:'bxlnszjy',title:'--',report:true},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'24',reg:'jxgzztzyjs',title:'--',report:true},
                        {name:'',id:'25',reg:'jxgzztkcjs',title:'--',report:true},
                        {name:'',id:'26',reg:'jxgzztktjxysjjx',title:'--',report:true},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'27',reg:'szdwjszrjsdw',title:'--',report:true},
                        {name:'',id:'28',reg:'szdwjzjsdw',title:'--',report:true},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'29',reg:'zyjsjxtrjjcss',title:'--',report:true},
                        {name:'',id:'30',reg:'zyjsjxzy',title:'--',report:true},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'31',reg:'zdjsyyxxqhz',title:'--',report:true},
                        {name:'',id:'32',reg:'zdjsyyxxxglzdjs',title:'--',report:true},
                        {name:'',id:'33',reg:'zdjsyyxzljk',title:'--',report:true},
                    ]
                },
                {
                    navName:[
                        {name:'',id:'34',reg:'xqffkxsfk',title:'--',report:true},
                        {name:'',id:'35',reg:'xqffkyrffk',title:'--',report:true},
                        {name:'',id:'36',reg:'xqffkqt',title:'--',report:true},
                    ]
                },
            ],
            reg: '',
            id: '',
            nid: '',
            backurl: {
                nid: '',
                reg: '',
                route: ''
            }
        }
    },
    methods: {
        action(data){
            console.log(data)
            this.$router.push({
                path: '/secondmenu',
                name: 'secondMenu',
                query: {
                    nav: data.nav
                }
            })
        },
        back(){
            this.$router.push({
                path: '/thirdmenu',
                name: 'thirdMenu',
                query: {
                    nid: this.backurl.nid,
                    reg: this.backurl.reg,
                    route: this.backurl.route
                }
            })
        },
        link(i){
            console.log(i)
            if(i.report){
                this.$router.push({
                    path: '/report',
                    name: 'report',
                    query: {
                        id: i.id,
                        title: i.title,
                        name: i.name,
                        reg: i.reg,
                    },
                })
            }else{
                //
                this.$router.push({
                    path: '/chartsdata',
                    name: 'chartsdata',
                    query: {
                        id: i.id,
                        name: i.name,
                        reg: i.reg,
                    },
                })
            }
        },
        screen(){
            if(window.innerWidth <= '340'){
                var screen = document.getElementsByClassName('menuList')
                console.log(screen)
                for(var i = 0;i < screen.length;i++){
                    screen[i].style.padding = '25px'
                }
            }
        }
    },
    mounted() {
        this.screen()
        console.log(this.$route.query)
        // localStorage.setItem("tid",this.$route.query.id)
        this.reg = this.$route.query.reg
        this.id = this.$route.query.id
        this.nid = this.$route.query.nid
        this.backurl.nid = this.$route.query.thirdId
        this.backurl.reg = this.$route.query.thirdReg
        this.backurl.route = this.$route.query.thirdRoute
        if(this.nid){
            this.menulist = this.navlist[this.nid-1].navName
            console.log(this.menulist)
            for(var i = 0;i < this.navlist[this.nid-1].navName.length;i++){
                this.navlist[this.nid-1].navName[i].reg = this.reg
            }
        }
        var _this = this
        setTimeout(function () {
            var menuListNum = document.getElementsByClassName('menuList')
            var thirdmenu = document.getElementsByClassName('fourthmenu')[0]
            var hiddenLi = document.createElement("li")
            var attr = document.createAttribute('style')
            attr.value = "width:150px;height:150px;margin-top:20px;visibility:hidden;"
            hiddenLi.setAttributeNode(attr)
            console.log(menuListNum,thirdmenu)
            if(menuListNum.length % 2 == 1){
                thirdmenu.appendChild(hiddenLi)
            }
            for(var i = 0;i < menuListNum.length;i++){
                menuListNum[i].style.background = _this.bgcolor[i]
            }
            _this.screen()
        },10)
    },
}
</script>

<style scoped>
#menu4{
    width: 100%;
    height: 100%;
}
.fourthmenu{
    /* width: 100%; */
    height: auto;
    padding: 0 20px;
    margin-bottom: 20px;
    display: flex;
    flex-flow:row wrap;
    justify-content: space-between;
}
.menuList{
    width: 70px;
    height: 70px;
    text-align: center;
    padding: 40px;
    margin-top: 20px;
    display:flex;
    align-items: center;
    border-radius: 4px;
    color: #ffffff;
}
.menuList span{
    display: block;
    width: 100%;
    text-align: center;
    font-size: 16px;
}
.action{
    padding: 20px;
    display: flex;
    justify-content: flex-end;
}
.action .quickbtn{
    margin-left: 20px;
}
</style>



