<template>
    <div id="report">
        <div class="bxln_bxdw zwzd">
            <div class="tabletitle">{{title}}</div>
            <div class="btn">
                <div class="fenpei">
                    <span class="name" style="font-size:14px;"></span>
                    <el-button class="s_btn" @click="fenpei(name1[0])" size="small" type="primary">{{name1[0].name}}</el-button>
                </div>
                <el-button class="search_btn" @click="searchData()" size="small" type="primary"></el-button>
            </div>
            <div class="table" v-if="zonghe">
                <div class="zdyj">
                    <div class="tit"></div>
                    <span class="count">({{zhContent.jielun.length}}/500)</span>
                    <div class="input_box"><textarea v-model="zhContent.jielun" name="jielun" id="" class="text_box" placeholder=""></textarea></div>
                </div>
                <div class="gjcs">
                    <div class="tit"></div>
                    <span class="count">({{zhContent.jianyi.length}}/200)</span>
                    <div class="input_box"><textarea v-model="zhContent.jianyi" name="jianyi" id="" class="text_box" placeholder=""></textarea></div>
                </div>
            </div>
            <div class="table" v-else>
                <div class="zdyj">
                    <div class="tit"></div>
                    <span class="count">({{content.yijian1.length}}/500)</span>
                    <div class="input_box"><textarea v-model="content.yijian1" name="yijian1" id="" class="text_box" placeholder=""></textarea></div>
                </div>
                <div class="gjcs">
                    <div class="tit"></div>
                    <span class="count">({{content.cuoshi1.length}}/200)</span>
                    <div class="input_box"><textarea v-model="content.cuoshi1" name="cuoshi1" id="" class="text_box" placeholder=""></textarea></div>
                </div>
                <div class="gjxg">
                    <div class="tit"></div>
                    <span class="count">({{content.xiaoguo1.length}}/200)</span>
                    <div class="input_box"><textarea v-model="content.xiaoguo1" name="xiaoguo1" id="" class="text_box" placeholder=""></textarea></div>
                </div>
            </div>
        </div>
        <div class="action">
            <mt-button class="backbtn" type="default" @click="back()"></mt-button>
            <mt-button class="quickbtn" type="default" size="normal" @click="sheetVisible=true"></mt-button>
            <mt-actionsheet :actions="data" v-model="sheetVisible">
            </mt-actionsheet>
        </div>
    </div>
</template>

<script>
export default {
    name: 'report',
    data() {
        return {
            sheetVisible: false,
            data: [
                {name:'',nav:'xqhxzb',method:this.action}, 
                {name:'',nav:'xxjcsj',method:this.action}, 
                {name:'',nav:'zyfzsj',method:this.action}, 
                {name:'',nav:'kcjssj',method:this.action}, 
                {name:'',nav:'szdwsj',method:this.action}, 
                {name:'',nav:'xsxxsj',method:this.action},
                {name:'',nav:'jcfx',method:this.action},
                {name:'',nav:'zwzdbg',method:this.action}
            ],
            title:'',
            zonghe:false,
            zhuanye:[{label:'',value:''},{label:'',value:''},{label:'',value:''},{label:'',value:''},{label:'',value:''}],
            zhibiao:[{label:'',value:''},{label:'',value:''},{label:'',value:''},{label:'',value:''},{label:'',value:''},{label:'',value:''},{label:'',value:''},{label:'',value:''},],
            peoples:[{label:'',value:''},{label:'',value:''},{label:'',value:''},{label:'',value:''},],
            name1:[
                {name:'',value:'1'},
                {name:'',value:'2'},
                {name:'',value:'3'},
                {name:'',value:'4'},
                {name:'',value:'5'},
                {name:'',value:'6'},
                {name:'',value:'7'},
                {name:'',value:'8'},
                {name:'',value:'9'},
                {name:'',value:'10'},
                {name:'',value:'11'},
                {name:'',value:'12'},
                {name:'',value:'13'},
                {name:'',value:'14'},
                {name:'',value:'15'},
                {name:'',value:'16'},
                {name:'',value:'17'},
            ],
            content:{
                yijian1:'',
                cuoshi1:'',
                xiaoguo1:'',
            },
            zhContent:{
                jielun:'',
                jianyi:'',
            }
        }
    },
    methods: {
        action(data){
            console.log(data)
            this.$router.push({
                path: '/secondmenu',
                name: 'secondMenu',
                query: {
                    nav: data.nav
                }
            })
        },
        back(){
            this.$router.go(-1)
        },
        fenpei(){

        },
        searchData(){
            
        }
    },
    mounted() {
        console.log(this.$route.query)
        this.title = this.$route.query.title + this.$route.query.name
        if(this.$route.query.name == ''){
            this.zonghe = true
            this.title = this.$route.query.name
        }
    },
}
</script>

<style>
#report{
    padding: 5px 15px;
}
#report .zwzd{
    display: flex;
    flex-direction: column;
}
#report .zwzd .tabletitle{
    width: 100%;
    text-align: center;
    margin: 0 auto;
    font-weight: 900;
}
#report .zwzd .btn{
    display: flex;
    margin: 10px 0;
}
#report .zwzd .btn .s_btn{
    margin-right: 40px;
}
#report .table{
    width: 100%;
    margin: 0 auto;
    border: 1px solid #e2e1e1;
}
#report .table .zdyj{
    border-bottom: 1px solid #e2e1e1;
    position: relative;
}
#report .table .gjcs{
    border-bottom: 1px solid #e2e1e1;
    position: relative;
}
#report .table .gjxg{
    position: relative;
}
#report .table .tit{
    height: 60px;
    line-height: 60px;
    padding-left: 20px;
    font-weight: 900;
    border-bottom: 1px solid #e2e1e1;
}
#report .table .input_box{
    flex: 1;
    height: 190px;
    margin: 10px;
    margin-left: 20px;
}
#report .table .input_box .text_box{
    width: 100%;
    height: 100%;
    resize: none;
    border: 0;
    padding: 0;
    outline: none;
}
#report .table .count{
    position: absolute;
    right: 5px;
    top: 40px;
    font-size: 14px;
    color: #aaaaaa; 
}
#report .action{
    padding: 20px;
    display: flex;
    justify-content: flex-end;
}
#report .action .quickbtn{
    margin-left: 20px;
}
</style>



