<template>
    <div id="charts4">
        <div class="chart" style="width:100%;height:auto;">
            <div class="shaixuan_box">
                <div style="display:inline-block;">
                    <span></span>
                    <el-select v-model="value[0].val" placeholder="" style="width:120px;" @change="getValue" size="small">
                        <el-option
                        v-for="item in options[0].data"
                        :key="item.key"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </div>
                <div style="display:inline-block;">
                    <el-select v-model="value[1].val" placeholder="" style="width:120px;" @change="getValue" size="small">
                        <el-option
                        v-for="item in options[1].data"
                        :key="item.key"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </div>
                <div style="display:inline-block;">
                    <span style="visibility:hidden;"></span>
                    <el-select v-model="value[2].val" placeholder="" style="width:120px;" @change="getValue" size="small">
                        <el-option
                        v-for="item in options[2].data"
                        :key="item.key"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </div>
                <div style="display:inline-block;">
                    <el-select v-model="value[3].val" placeholder="" style="width:120px;" @change="getValue" size="small">
                        <el-option
                        v-for="item in options[3].data"
                        :key="item.key"
                        :label="item.label"
                        :value="item.value">
                        </el-option>
                    </el-select>
                </div>
            </div>
            <div class="back" @click="backcharts()" ref="back"></div>
            <div :id="op.id" ref="myChart" style="width:100%;height:350px;margin:0 auto;"></div>
            <div class="table" v-if="table">
                <div class="tabletit">{{tabletitle1}}</div>
                <el-table :data="tableData1.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%">
                    <el-table-column fixed prop="date" label="" width="150"></el-table-column>
                    <el-table-column prop="name" label=""></el-table-column>
                    <el-table-column prop="province" label=""></el-table-column>
                    <el-table-column prop="city" label="" width="120"></el-table-column>
                    <el-table-column prop="address" label="" width="300"></el-table-column>
                    <el-table-column prop="zip" label="" width="120"></el-table-column>
                </el-table>
                <el-pagination background 
                    layout="prev, pager, next, sizes, total"
                    :page-sizes="[5, 10]"
                    :page-size="pagesize"
                    :total="tableData1.length"
                    @current-change="handleCurrentChange" 
                    @size-change="handleSizeChange"
                    style="margin-top:10px;">
                </el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
import { Button,Toast } from 'mint-ui';
export default {
    name: 'charts4',
    props:['op'],
    data(){
        return{
            backbtn: null,
            table: false,
            tapnum: 0,      //
            list: [],
            pagesize: 10,
            currpage: 1,
            tabletitle1: '1',
            tableData1: [{
                date: '2016-05-03',
                name: '',
                province: '',
                city: '',
                address: ' 1518 ',
                zip: 200333
            }, {
                date: '2016-05-02',
                name: '',
                province: '',
                city: '',
                address: ' 1518 ',
                zip: 200333
            }, {
                date: '2016-05-04',
                name: '',
                province: '',
                city: '',
                address: ' 1518 ',
                zip: 200333
            }, {
                date: '2016-05-01',
                name: '',
                province: '',
                city: '',
                address: ' 1518 ',
                zip: 200333
            }, {
                date: '2016-05-08',
                name: '',
                province: '',
                city: '',
                address: ' 1518 ',
                zip: 200333
            }, {
                date: '2016-05-06',
                name: '',
                province: '',
                city: '',
                address: ' 1518 ',
                zip: 200333
            }, {
                date: '2016-05-07',
                name: '',
                province: '',
                city: '',
                address: ' 1518 ',
                zip: 200333
            }],
            baroption: {
                color: ['#029348'],
                title: {
                    text: '',
                    left:'center'
                },
                tooltip : {
                    trigger: 'axis',
                    axisPointer : {            // 
                        type : 'shadow'        // 'line' | 'shadow'
                    }
                },
                xAxis : [
                    {
                        type : 'value',
                        // name: '(%)'
                    }
                ],
                yAxis : [
                    {
                        type : 'category',
                        triggerEvent:true,
                        nameLocation: 'middle',
                        // data : ['','',''],
                        axisTick: {
                            alignWithLabel: true,
                            inside: true,
                        },
                        axisLabel: {
                            show: true,
                            interval:0,
                            fontSize:10,
                            align: 'center',
                            padding: [0,20,0,0],
                            formatter:function(params) {
                                var newParamsName = "";
                                var paramsNameNumber = params.length;
                                var provideNumber = 4;
                                var rowNumber = Math.ceil(paramsNameNumber / provideNumber);
                                if (paramsNameNumber > provideNumber) {
                                    for (var p = 0; p < rowNumber; p++) {
                                        var tempStr = "";
                                        var start = p * provideNumber;
                                        var end = start + provideNumber;
                                        if (p == rowNumber - 1) {
                                            tempStr = params.substring(start, paramsNameNumber);
                                        } else {
                                            tempStr = params.substring(start, end) + "\n";
                                        }
                                        newParamsName += tempStr;
                                    }

                                } else {
                                    newParamsName = params;
                                }
                                return newParamsName
                            },
                        },
                    }
                ],
                series : [
                    {
                        // name:'',
                        // type:'bar',
                        // barWidth: '60%',
                        // data:[80,50,20],
                        itemStyle: {
                            normal: {
                                label: {
                                    show: true,     //
                                    position: 'right',  //
                                    textStyle: {        //
                                        color: '#029348',
                                        fontSize: 10
                                    }
                                }
                            }
                        },
                    }
                ]
            },
            options:[
                {
                    data:[{
                        key:'1',
                        value: [],
                        label: ''
                    },{
                        key:'2',
                        value: [],
                        label: ''
                    }]
                },{
                    data:[{
                        key:'1',
                        value: [],
                        label: ''
                    },{
                        key:'2',
                        value: [],
                        label: ''
                    }]
                },{
                    data:[{
                        key:'1',
                        value: [],
                        label: ''
                    },{
                        key:'2',
                        value: [],
                        label: ''
                    }]
                },{
                    data:[{
                        key:'1',
                        value: [],
                        label: ''
                    },{
                        key:'2',
                        value: [],
                        label: ''
                    }]
                },
            ],
            value:[{
                val:'',
            },{
                val:'',
            },{
                val:'',
            },{
                val:'',
            }]
        }
    },
    methods: {
        getValue(value){
            console.log(value)
            var mychart = this.$echarts.init(document.getElementById(value[0]))
            
        },
        handleClick(tab, event) {
            // console.log(tab, event);
        },
        formatter(row, column) {
            return row.address;
        },
        handleCurrentChange(cpage) {
            this.currpage = cpage;
        },
        handleSizeChange(psize) {
            this.pagesize = psize;
        },
        drawCharts(){       //
            let myChart = this.$echarts.init(document.getElementById(this.op.id))
            myChart.setOption(this.baroption,true)
        },
        backcharts(){       //
            var _this = this
            _this.table = false
            _this.tapnum = 0
            let myChart = this.$echarts.init(document.getElementById(this.op.id));
            this.baroption.title.text = this.op.title.text
            this.baroption.xAxis[0].name = this.op.xAxis[0].name
            this.baroption.yAxis[0].data = this.op.yAxis[0].data
            this.baroption.series[0].name = this.op.series[0].name
            this.baroption.series[0].type = this.op.series[0].type
            this.baroption.series[0].barWidth = this.op.series[0].barWidth
            this.baroption.series[0].data = this.op.series[0].data
            this.drawCharts()
            var backbtn = document.getElementsByClassName('back')
            backbtn[_this.op.num-1].style.display = 'none'
        },
    },
    mounted(){
        console.log(this.op)
        for(var i = 0;i < this.options.length;i++){     //idoptions
            for(var j = 0;j < this.options[i].data.length;j++){
                this.options[i].data[j].value[0] = this.op.id
            }
        }
        this.baroption.title.text = this.op.title.text
        this.baroption.xAxis[0].name = this.op.xAxis[0].name
        this.baroption.yAxis[0].data = this.op.yAxis[0].data
        this.baroption.series[0].name = this.op.series[0].name
        this.baroption.series[0].type = this.op.series[0].type
        this.baroption.series[0].barWidth = this.op.series[0].barWidth
        this.baroption.series[0].data = this.op.series[0].data
        this.drawCharts()
        let myChart = this.$echarts.init(document.getElementById(this.op.id));
        var _this = this
        myChart.on('click',function(params){
            _this.tapnum++
            console.log(_this.tapnum,params)
            var backbtn = document.getElementsByClassName('back')
            backbtn[_this.op.num-1].style.display = 'block'
            if(_this.tapnum == 2){      //elseif
                _this.table = true
            }else{
                if(params.componentType == 'yAxis'){
                    _this.baroption.yAxis[0].data = ['2016','2017','2018','2019']
                    _this.baroption.series[0].data = [152,45,201,95]
                    myChart.setOption(_this.baroption,true)
                }else if(params.componentType == 'series'){
                    _this.baroption.yAxis[0].data = ['2016','2017','2018','2019']
                    _this.baroption.series[0].data = [152,45,201,95]
                    myChart.setOption(_this.baroption,true)
                }
            }
            
        })
    }
}
</script>

<style>
#charts4{
    width: 100%;
    height: auto;
}
#charts4 .chart{
    position: relative;
}
#charts4 .chart .shaixuan_box{
    margin: 10px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
}
#charts4 .chart .shaixuan_box>div{
    margin-top: 10px;
}
#charts4 .back{
    position: absolute;
    right: 7%;
    top: 118px;
    padding: 4px 10px;
    border-radius: 4px;
    color: #ffffff;
    font-size: 14px;
    background: #26a2ff;
    z-index: 100;
    display: none;
}
#charts4 .back:active{
    background: #eeeeee;
}
</style>



