            localStorage.setItem("tid",i.id)
            if(i.fourmenu){
                //
                this.$router.push({
                    path: '/fourthmenu',
                    name: 'fourthMenu',
                    query: {
                        id: i.id,
                        nid: i.fourmenu,
                        name: i.name,
                        reg: i.reg,
                        thirdId: this.nid,
                        thirdReg: this.reg,
                        thirdRoute: this.backUrl
                    }
                })
            }else if(i.card){
                //
                this.$router.push({
                    path: '/card',
                    name: 'card',
                    query: {
                        id: i.id,
                        name: i.name,
                        reg: i.reg,
                    },
                })
            }else if(i.report){
                this.$router.push({
                    path: '/report',
                    name: 'report',
                    query: {
                        id: i.id,
                        title: i.title,
                        name: i.name,
                        reg: i.reg,
                    },
                })
            }else{
                //
                if(i.select){
                    this.$router.push({
                        path: '/chartsdata',
                        name: 'chartsdata',
                        query: {
                            id: i.id,
                            name: i.name,
                            reg: i.reg,
                            select: i.select
                        },
                    })
                }else{
