        this.backUrl = this.$route.query.route
        if(this.nid){
            this.menulist = this.navlist[this.nid-1].navName
            console.log(this.menulist)
            for(var i = 0;i < this.navlist[this.nid-1].navName.length;i++){     //regnavlist
