            var thirdmenu = document.getElementsByClassName('thirdmenu')[0]
            var hiddenLi = document.createElement("li")
            var attr = document.createAttribute('style')
            attr.value = "width:150px;height:150px;margin-top:20px;visibility:hidden;"
            hiddenLi.setAttributeNode(attr)
            console.log(menuListNum,thirdmenu)
            if(menuListNum.length % 2 == 1){
                thirdmenu.appendChild(hiddenLi)
            }
            for(var i = 0;i < menuListNum.length;i++){
                menuListNum[i].style.background = _this.bgcolor[i]
            }
            _this.screen()
        },10)
