    height: auto;
    margin-top: 20px;
}
#card .box-card .el-card__header{
    padding: 0px;
    font-size: 14px;
    height: 60px;
    color: #ffffff;
}
#card .box-card .el-card__header .clearfix{
    display: block;
    height: 100%;
    padding: 0 10px;
    display: flex;
    align-items: center;
    justify-content: center;
}
#card .box-card .el-card__body{
    padding: 10px;
    height: 58px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
#card .box-card .el-card__body .text{
    font-weight: 900;
}
#card .box-card .el-card__body .center{
    text-align: center;
}
#card .action{
    padding: 20px;
    display: flex;
    justify-content: flex-end;
}
#card .action .quickbtn{
    margin-left: 20px;
}
</style>



